import React from "react";

class About extends React.Component {
  constructor() {
    super();
    this.state = {
      skills: [
        {
          id: "JavaScript_skill",
          content: "JavaScript",
          percentage: "5%",
          value: "5"
        },
        {
          id: "VueJS_skill",
          content: "VueJS",
          percentage: "2%",
          value: "2"
        },
        {
          id: "NodeJS_skill",
          content: "NodeJS",
          percentage: "10%",
          value: "10"
        },
        {
          id: "CouchBase_skill",
          content: "CouchBase",
          percentage: "15%",
          value: "15"
        },
        {
          id: "Git_skill",
          content: "Git",
          percentage: "60%",
          value: "60"
        },
        {
          id: "Archlinux_skill",
          content: "Arch Linux",
          percentage: "95%",
          value: "95"
        }
      ],
      about_me: [
        {
          id: "first-p-about",
          content:
            "Hello, I'm Loïc. I am currently studying BSc Software Engineering at Université des Mascareignes. I spend my time usually doing research, and learning new technologies"
        }
      ]
    };
  }

  render() {
    return (
      <section id="about" className="about-mf sect-pt4 route">
        <div className="container">
          <div className="row">
            <div className="col-sm-12">
              <div className="box-shadow-full">
                <div className="row">
                  <div className="col-md-6">
                    <div className="row">
                      <div
                        className="col-sm-6 col-md-5"
                        style={{ margin: "0 auto" }}
                      >
                        <div
                          className="about-img"
                          style={{ textAlign: "center" }}
                        >
                          <img
                            className="img-fluid rounded b-shadow-a"
                            alt=""
                          />
                        </div>
                      </div>
                    </div>
                    <div className="skill-mf">
                      <p className="title-s">Skills</p>
                      {this.state.skills.map(skill => {
                        return (
                          <React.Fragment key={skill.id}>
                            <span>{skill.content}</span>{" "}
                            <span className="pull-right">
                              {skill.percentage}
                            </span>
                            <div className="progress">
                              <div
                                className="progress-bar"
                                role="progressbar"
                                style={{ width: skill.percentage }}
                                aria-valuenow={skill.value}
                                aria-valuemin="0"
                                aria-valuemax="100"
                              ></div>
                            </div>
                          </React.Fragment>
                        );
                      })}
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="about-me pt-4 pt-md-0">
                      <div className="title-box-2">
                        <h5 className="title-left">About Me</h5>
                      </div>
                      {this.state.about_me.map(content => {
                        return (
                          <p className="lead" key={content.id}>
                            {content.content}
                          </p>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;
